<template>
  <div class="id_card_upload">
      <div class="pay_way_title">任务图片上传</div>
    <van-row class="upload_hint text-desc" style="color: darkred">
      <van-icon name="hint"/>
      请点击下方图标上传图片，可多次点击上传，最多上传8张图片，默认第一张图为主图！
    </van-row>
    <div class="upload-img-5">
      <p style="font-size: 12px"></p>
      <div class="task-upload-list clearfix">
        <div class="u-item" v-for="(item,nn) in taskPic" :key="nn">
          <img :src="item.content" alt="图片" class="imgPreview">
          <van-icon name="close" @click="delImg(nn)" class="task-pic-del"/>
        </div>
        <van-uploader :after-read="onRead" :accept="'image/*'"  v-show="taskPic.length<=7">
          <img :src="uploadBtn" alt="等待传图" class="imgTipBtn">
        </van-uploader>
      </div>
    </div>
  </div>
</template>
<script>
import { Row, Col, Uploader, ImagePreview,Toast } from "vant";

export default {
  name: "uploadNew",
  data() {
    return {
      uploadBtn: require(`@/assets/images/upload_btn.png`),
      taskPic:[],
      oFile: [],
      isPic:'0',
      files:{
        taskFile:[],
      }
    }
  },
  created() {

  },
  methods: {
    onRead (files) { // 上传图片到图片服务器 //multiple
      this.taskPic.push(files)  // postData是一个数组
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        console.dir(f1)
        that.oFile.push(f1.file)
        console.dir(that.oFile)
        that.files.taskFile = that.oFile;
      }
      this.$emit('setFiles',this.files);
      // let url = API + '/upload?type=99'
      // let fd = new FormData()
      // fd.append('upImgs', file.file)
      // this.axios.post(url, fd, {headers: {
      //     'Content-Type': 'multipart/form-data'
      //   }}).then(res => {
      //   this.imgUrlListValue.push(res.data.urls[0].image) //这里上传到指定的图片服务器，成功后会返回图片的url
      // }).catch(err => {
      //   alert(err)
      // })
    },
    delImg (index) {
      // 删除指定下标的图片对象
      if (isNaN(index) || index >= this.taskPic.length) {
        return false
      }
      let tmp = []
      let otmp = [];
      for (let i = 0, len = this.taskPic.length; i < len; i++) {
        if (this.taskPic[i] !== this.taskPic[index]) {
          tmp.push(this.taskPic[i])
          otmp.push(this.oFile[i])
        }
      }
      this.taskPic = tmp
      this.oFile = otmp
      this.files.taskFile = this.oFile;
      this.$emit('setFiles',this.files);
    },
    // onRead1(files){
    //   let img = new Image();
    //   img.src = files.content;
    //   let that=this;
    //   img.onload=function(){
    //     let f1 = that.compress(img);
    //     that.files.upFile = f1;
    //     that.up_pic = f1.content
    //   }
    //   this.$emit('setFiles',this.files);
    // },
    // 压缩图片
    compress(img){
        let originWidth = img.width, // 压缩后的宽
        originHeight=img.height,
        maxWidth = 600, maxHeight = 400,
        quality = 0.8, // 压缩质量
        canvas = document.createElement('canvas'),
        drawer = canvas.getContext("2d");
        canvas.width = maxWidth;
        canvas.height = originHeight/originWidth*maxWidth;
        drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
        let base64 = canvas.toDataURL("image/jpeg", quality); // 压缩后的base64图片
        let file = this.dataURLtoFile(base64,Date.parse(Date())+'.jpg');
        file={content:base64,file:file};
        //console.log('压缩后的file');
        //console.log(file);//压缩后的file
        return file;
    },
     // base64转file
    dataURLtoFile(dataurl,filename){
      let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr],filename,{type:mime});
    },
    // 图片超出限制后 进行压缩
    maxReadBack(files){
      Toast('图片不能超出5M')
    }
  },
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [ImagePreview.name]: ImagePreview,
    [Uploader.name]: Uploader,
    [Toast.name]: Toast
  }
}
</script>
<style lang="scss" scoped>
@import "../../../assets/scss/trans";
.task-upload-list {
   padding:10px 0;
}
.task-upload-list .u-item{
    float:left;
    width:23%;
    height:100px;
    padding:1%;
    position:relative;
  marbin-bottom:20px;
}
.task-upload-list .imgPreview {
  width:100%;
  height:100px;
}.task-upload-list .imgTipBtn {
  width:80px;
  height:80px;
}
.task-upload-list .task-pic-del {
  position:absolute;
  top:8px;
  right:8px;
  font-size:20px;
  color:#f00;
  z-index:11;
}
.video-js {
  display:inline-block;
}
.pay_way_title {
  font-weight: bold;
  padding: 0;
}
.id_card_upload {
  margin: 5px 0 5px 0;
  background-color: #fff;
  padding: 15px;
  > div {
    margin-bottom: 15px;
  }
}
.upload_hint {
  position: relative;
  padding-left: 20px;
  line-height: 1.6;
  i {
    position: absolute;
    top: 2px;
    left: 0;
  }
}
.id_card_row {
  > div {
    text-align: center;
  }
  .text-desc {
    margin-bottom: 8px;
  }
  .red_color {
    color: darkred;
    font-size: 14px;
    font-weight: 700;
  }
}
.upload_box .upload_wrap {
  position: relative;
  border: 1px dashed $gray;
  min-height: 145px;
  box-sizing: border-box;
  padding: 5px;
  text-align: center;
  img {
    max-width: 100%;
    height: 120px;
  }
  .van-uploader {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  .add_btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $gray;
    p {
      white-space: nowrap;
      margin: 0;
      font-size: 18px;
    }
    i {
      font-size: 26px;
    }
  }
}
</style>





